@import "core/reset";
@import "core/variables";
@import "layout/header";
@import "layout/hero";

body {
  font-family: var(--secondary-font);
  line-height: 24px;
}

.wrapper {
  margin: auto;
  width: 100%;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
  position: relative;
}

.motto {
  display: flex;
  justify-content: space-around;
  background-color: white;
  width: 15.63rem;
  height: 4.063rem;
  margin: auto;
  padding: 0.9375rem;
  transform: translate(0, -50%);
  font-weight: 600;
  font-size: 1.25rem;
  align-items: center;
  font-style: italic;
}

/* TEAM */

#who {
  transform: translate(0, -50px);
}

.team {
  margin-bottom: 1.875rem;
}

.title-team {
  font-family: var(--primary-font);
  font-size: 1.5rem;
  color: #14d9c4;
  font-weight: 500;
}

.bite {
  position: relative;
  width: 30px;
  height: 30px;
  background-color: white;
  transform: translate(0, 150%) rotate(45deg);
  margin: auto;
  z-index: 14;
}
.strengths-weaknesses {
  background: var(--grey-color);
  padding-top: 1.875rem;
  padding-bottom: 2.813rem;
  width: 100%;
}

.strengths {
  padding-bottom: 30px;
}

.title-secondary {
  font-size: 1.375rem;
  font-family: var(--primary-font);
  color: var(--primary-color);
  line-height: 45px;
}

/* WHO WE ARE */

.who {
  padding-top: 1.875rem;
}

.title-who {
  font-family: var(--primary-font);
  font-size: 1.5rem;
  color: var(--primary-color);
  font-weight: 500;
  padding-bottom: 1.875rem;
}

.image-container {
  width: 180px;
  height: 180px;
  border: 0.3125rem solid var(--grey-border);
  border-radius: 0.25rem;
  overflow: hidden;
  margin: auto;
}

.title-people {
  font-size: 1.25rem;
  line-height: 45px;
  font-weight: normal;
}
.card {
  padding-bottom: 2.813rem;
}

.sm {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-top: 15px;
}
.icons {
  color: var(--primary-color);
}
.sm a:nth-child(1):after,
.sm a:nth-child(2):after,
.sm a:nth-child(3):after {
  color: var(--grey-links-footer);
  content: "Â·";
  margin: 0.5rem;
}

/* FOOTER */

.footer-border {
  border-top: 1px solid var(--grey-footer);
}
.copyright {
  font-size: 0.875rem;
  line-height: 1.25rem;
  color: var(--grey-footer);
  font-family: var(--secondary-font);
  padding-top: 1.875rem;
  padding-bottom: 1.875rem;
}

.footer-links a {
  list-style-type: none;
  line-height: 1.25rem;
  font-size: 0.875rem;
  color: var(--grey-links-footer);
  font-weight: bolder;
  text-decoration: none;
}

.logo-adalab {
  max-width: 7.25rem;
  max-height: 2.813rem;
  padding-top: 1.875rem;
  margin: auto;
}

.main-footer {
  padding-bottom: 2.813rem;
  padding-top: 1.875rem;
}

@media all and (min-width: 768px) {
  .motto {
    opacity: 0.8;
    margin-left: 46px;
    transform: translate(0, -100%);
  }

  /* MAIN */
  .wrapper,
  .wrapper2 {
    margin: auto;
    width: 100%;
    padding-left: 45px;
    padding-right: 45px;
  }
  /* TEAM */
  #who {
    transform: translate(0, 160px);
  }
  .team {
    margin-bottom: 60px;
  }
  .bite {
    transform: translate(0, 250%) rotate(45deg);
  }
  .strengths-weaknesses {
    display: flex;
    align-items: flex-start;
  }
  .weaknesses {
    height: 268.8px;
  }
  .left {
    display: flex;
    align-items: flex-start;
  }
  .image-container {
    width: 206px;
    height: 206px;
    flex-shrink: 0;
  }
  .left .info-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-left: 31px;
    width: 440px;
  }
  .left .info-container p {
    text-align: left;
  }
  .right .info-container p {
    text-align: right;
  }
  .sm {
    margin-top: 15px;
  }
  .right {
    display: flex;
    flex-direction: row-reverse;
  }
  .right .info-container {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-right: 31px;
    width: 440px;
  }

  /* FOOTER */
  .main-footer {
    display: flex;
    justify-content: space-between;
    height: 10.63rem;
  }
  .copyright {
    padding-top: 0.125rem;
    position: relative;
  }
  .footer-links {
    order: 3;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    width: 20.25rem;
    height: 5.625rem;
  }
  .footer-links li {
    padding: 3px;
  }
  .logo-adalab {
    padding-top: 0px;
    position: absolute;
    transform: translate(-9px, 100%);
  }
}
@media all and (min-width: 1200px) {
  .wrapper2 {
    margin: auto;
    width: 100%;
    padding-left: 138px;
    padding-right: 138px;
  }
  .wrapper {
    max-width: 924px;
  }

  /* WHO */
  .image-container {
    width: 278px;
    height: 278px;
    margin: 0px;
  }
  .left .info-container {
    margin-left: 45px;
  }
  .right .info-container {
    margin-right: 45px;
  }
}
